import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Main from './views/Main.vue'


import FaqEdit from './views/FaqEdit.vue'
import FaqList from './views/FaqList.vue'

import HeroEdit from './views/HeroEdit.vue'
import HeroList from './views/HeroList.vue'

import ArticleEdit from './views/ArticleEdit.vue'
import ArticleList from './views/ArticleList.vue'


import HomeEdit from './views/HomeEdit.vue'
import HomeList from './views/HomeList.vue'


import AdminUserEdit from './views/AdminUserEdit.vue'
import AdminUserList from './views/AdminUserList.vue'


Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/login', name: 'login', component: Login, meta: { isPublic: true } },
    {
      path: '/',
      name: 'main',
      component: Main,
      children: [        
        { path: '/hero/create', component: HeroEdit },
        { path: '/hero/edit/:id', component: HeroEdit, props: true },
        { path: '/hero/list', component: HeroList },

        { path: '/faq/create', component: FaqEdit },
        { path: '/faq/edit/:id', component: FaqEdit, props: true },
        { path: '/faq/list', component: FaqList },


        { path: '/articles/create', component: ArticleEdit },
        { path: '/articles/edit/:id', component: ArticleEdit, props: true },
        { path: '/articles/list', component: ArticleList },


        { path: '/homes/create', component: HomeEdit },
        { path: '/homes/edit/:id', component: HomeEdit, props: true },
        { path: '/homes/list', component: HomeList },

        { path: '/admin_users/create', component: AdminUserEdit },
        { path: '/admin_users/edit/:id', component: AdminUserEdit, props: true },
        { path: '/admin_users/list', component: AdminUserList },
      ]
    },
  ],
  mode: 'hash'
})
router.beforeEach((to, from ,next) => {
  if (!to.meta.isPublic && !localStorage.token) {
    return next('/login')
  }
  next()
})
export default router